import React, { useEffect, useMemo, useState } from 'react'
import { CustomButton } from '../../globalComponents'
import moment from 'moment-timezone'
import { getBadgeColor } from '../../../utils/common'
import { CustomTable } from '../../reuseableComponents'
import { useCustomTranslation } from '../../../translation'
const DesignationTable = (props) => {
  const {t}=useCustomTranslation()
  const {
    designationList,
    isLoading,
    setSelectedDesignation,
    toggleDesignation,
    onHandleDeleteModal,
    setCurrentPage,
    currentPage
  } = props

  const [desiList, setDesiList] = useState()
  const updateDesignation = (data) => {
    setSelectedDesignation(data)
    toggleDesignation()
  }

  useEffect(() => {
    setDesiList(designationList)
  }, [designationList])


  const scopedSlots = useMemo(() => ({
    sn: (_, index) => (
      <span>{index + 1}</span>
    ),
    is_active: item => (
      <span
        className={`badge ${getBadgeColor(item.is_active ? "Active" : "Inactive")}`}
      >
        {item.is_active ? "Active" : "Inactive"}
      </span>
    ),
    created_at: item => moment(item.created_at).format("DD-MM-YYYY"),
    action: item => (
      <div className="actionGroup d-flex gap-2">
        <CustomButton
          text={t("btns.update")}
          classNames="btn btn-primary"
          handleClick={() => {
            updateDesignation(item);
          }}
        />
        {item?.emp_count===0&&<CustomButton
          text={t("btns.delete")}
          handleClick={() => onHandleDeleteModal(item._id)}
          classNames="btn btn-danger"
        />}
      </div>
    )
  }), [
    setSelectedDesignation,
    toggleDesignation,
    onHandleDeleteModal
  ]);
  const fields = [
    { key: 'sn', label: 'S.N' },
    { key: 'department_name', label: 'Department Name' },
    { key: 'name', label: 'Name' },
    { key: 'short_name', label: 'Short Name' },
    { key: 'emp_count', label: 'Total Employees' },
    { key: 'is_active', label: 'Status' },
    { key: 'created_at', label: 'Created At' },
    { key: 'action', label: 'Action' }
  ];


  return (
    <>
      <CustomTable
        data={desiList?.data}
        fields={fields}
        isLoading={isLoading}
        pagination={true}
        currentPage={currentPage}
        setCurrentPage={setCurrentPage}
        totalPages={desiList?.totalPages}
        tableName={"Designation"}
        // onSearch={handleSearch}
        scopedSlots={scopedSlots}
      />
    </>

  )
}

export default DesignationTable
