import React from 'react'
import Lottieplayer from '../commanComponent/Lottieplayer'
import backup from '../../../src/images/Backup.json'
import { CustomButton } from '../globalComponents'
import PropTypes from 'prop-types'
import { useCustomTranslation } from '../../translation'

/**
 * ApproveRejectModal component.
 * @param {string} [title="Are You Sure?"] - Title of the modal (optional, defaults to "Are You Sure?")
 * @param {string} [desc] - Description text (optional).
 * @param {function} onApproveReject - Callback function for handling approve/reject actions (required).
 * @param {boolean} isShow - Boolean flag to show or hide the modal (required).
 * @param {boolean} [btnRightLoading=false] - Boolean flag to show loading state for the right button (optional).
 * @param {string} [btnTextLeft="Cancel"] - Custom text for the left button (optional).
 * @param {string} [btnTextRight="Confirm"] - Custom text for the right button (optional).
 * @returns {void}
 */
const ApproveRejectModal = (
  {  
    title ,               
    desc = "",                           
    onApproveReject,                      
    isShow,                               
    btnLeftLoading = false,               
    btnRightLoading = false,              
    btnTextLeft ,               
    btnTextRight     
}
) => {
    const {t} =useCustomTranslation()
    return (
        <>
            <div className="modal fade attendanceViewodal" id="sureMdl" style={{ display: isShow ? "unset !important" : "none !important", opacity: isShow ? "unset" : "0" }}>
                <div className="modal-dialog modal-dialog-centered">
                    <div className="modal-content rounded-1 border-0">
                        <div className="modal-body ">
                            <div className="text-center suremodalCon">
                                <div className="d-flex justify-content-center">
                                    <Lottieplayer lottieUrl={backup} Lottiestyle={{ width: "90px", height: "90px" }} />
                                </div>
                                <h6 className="fw-semibold mb-2">{title?title:t("alerts.deletePopup.title")}</h6>
                                <p className="mb-5 px-4 text-center">{desc ? desc : ""}
                                </p>
                                <div className="d-flex justify-content-center gap-3">
                                    <CustomButton
                                        classNames={"btn btn-danger"}
                                        isLoading={btnLeftLoading}
                                        text={btnTextLeft?btnTextLeft:t("btns.reject") }
                                        handleClick={() => onApproveReject(false)}
                                    />
                                    <CustomButton
                                        classNames={"btn btn-success"}
                                        isLoading={btnRightLoading}
                                        text={btnTextRight?btnTextRight:t("btns.confirm")}
                                        handleClick={() => onApproveReject(true)}
                                    />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            {isShow && <div className="modal-backdrop fade show"></div>}
        </>
    )
}
// Add PropTypes validation
ApproveRejectModal.propTypes = {
    title: PropTypes.string,                     
    desc: PropTypes.string,                       
    onApproveReject: PropTypes.func.isRequired,   
    isShow: PropTypes.bool.isRequired,            
    btnRightLoading: PropTypes.bool,                         
    btnTextLeft: PropTypes.string,                
    btnTextRight: PropTypes.string,   
};

export default ApproveRejectModal
