import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux'
import { CustomButton, CustomError } from '../../globalComponents';
import { departmentAction, designationAction } from '../../../core/resource/actions';
import { addDesignationSchema } from '../../../utils/ValidationSchema';
import { validateSchema } from '../../../utils/ValidationHelper';
import { useCustomTranslation } from '../../../translation';
import { CustCheckbox, Input, Label } from '../../commanComponent';
import { createShortName } from '../../../utils/common';

const CreateUpdateDesignation = (props) => {
    const { t } = useCustomTranslation()
    const {
        getAllDesignation,
        setSelectedDesignation,
        isOpen,
        toggleDesignation,
        selectedDesignation,
    } = props
    const [errors, setErrors] = useState({});
    const dispatch = useDispatch();
    const { addDesignationPending, addDesignationRes, addDesignationError } = useSelector(state => state.designation);
    const { getDepartmentListPending, getDepartmentListRes, getDepartmentListError } = useSelector(state => state.department);
    const { updateDesignationPending, updateDesignationRes, updateDesignationError } = useSelector(state => state.designation);
    const [departmentList, setDepartmentList] = useState([])
    const [isActive, setIsActive] = useState(true);
    const [designaitonId, setDesignationId] = useState('')
    const [designationFromData, setDesignationFromData] = useState({
        departmentId: '',
        designationName: '',
        shortName: '',
    })

    useEffect(() => {
        getAllDepartment()
    }, [])

    const getAllDepartment = () => {
        dispatch(departmentAction.getDepartmentListRequest());
    }
    useEffect(() => {
        if (!getDepartmentListPending) {
            if (getDepartmentListError) {
                return;
            }
            if (getDepartmentListRes) {
                if (getDepartmentListRes?.code === 200) {
                    const departmentResposnse = getDepartmentListRes?.data
                    setDepartmentList(departmentResposnse)
                }
            }
        }
    }, [
        getDepartmentListRes,
        getDepartmentListError,
        getDepartmentListPending
    ]);


    const createDesignation = async () => {
        const validationResponse = await validateSchema(addDesignationSchema, designationFromData);
        if (validationResponse.isValid) {
            setErrors({});
            let params = {
                department_id: designationFromData?.departmentId,
                name: designationFromData?.designationName || '',
                short_name: designationFromData?.shortName || '',
                is_active: isActive

            }
            if (selectedDesignation) {
                params["_id"] = designaitonId
                dispatch(designationAction.updateDesignationRequest(params));
            } else {

                dispatch(designationAction.addDesignationRequest(params));
            }
        } else {
            setErrors(validationResponse.errors);
        }
    }

    useEffect(() => {
        if (!addDesignationPending) {
            if (addDesignationError) {
                dispatch(designationAction.handleClearDesignationStore('HANDLE_CLEAR_ADD_DESIGNATION_STORE'))
                return;
            }
            if (addDesignationRes) {
                if (addDesignationRes?.code == 200) {
                    dispatch(designationAction.handleClearDesignationStore('HANDLE_CLEAR_ADD_DESIGNATION_STORE'))
                    toggleDesignation()
                    getAllDesignation()
                }
            }
        }
    }, [
        addDesignationPending,
        addDesignationRes,
        addDesignationError,
    ]);

    const handleChange = (event) => {
        errors && setErrors({});
        const { name, value } = event.target

        setDesignationFromData(prevState => {
            const updatedState = {
                ...prevState,
                [name]: value,
            };
            if (name === 'designationName') {
                updatedState.shortName = createShortName(value);
            }
            return updatedState;
        });

    }

    //update designation 
    useEffect(() => {
        setDesignationFromData({
            departmentId: selectedDesignation?.department_id,
            designationName: selectedDesignation?.name || '',
            shortName: selectedDesignation?.short_name || '',
        })
        setIsActive(selectedDesignation?.is_active)
        setDesignationId(selectedDesignation?._id)
    }, [selectedDesignation])


    useEffect(() => {
        if (!updateDesignationPending) {
            if (updateDesignationError) {
                dispatch(designationAction.handleClearDesignationStore('HANDLE_CLEAR_UPDATE_DESIGNATION_STORE'))
                return;
            }
            if (updateDesignationRes) {
                if (updateDesignationRes?.code == 200) {
                    dispatch(designationAction.handleClearDesignationStore('HANDLE_CLEAR_UPDATE_DESIGNATION_STORE'))
                    toggleDesignation();
                    getAllDesignation();
                }
            }
        }
    }, [
        updateDesignationPending,
        updateDesignationRes,
        updateDesignationError
    ]);



    useEffect(() => {
        if (isOpen == false) {
            setDesignationFromData({
                departmentId: '',
                designationName: '',
                shortName: ''
            })
            setSelectedDesignation('')
            setErrors({});
            // setDesignationId()
        }
    }, [isOpen])


    return (
        <>
            <div className={`offcanvas offcanvas-end creatNewTicketOff ${isOpen ? 'show' : ''}`} tabIndex={-1}
                style={{ visibility: isOpen ? 'visible' : 'hidden' }}
            >
                <div className="offcanvas-header">
                    <h5 className="offcanvas-title  fs-5 fw-bold text-primary">{selectedDesignation ? "Update" : "Create New"} Designation</h5>
                    <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"
                        onClick={() => toggleDesignation()}
                    />
                </div>
                <div className="offcanvas-body">
                    <div className="formPanel">
                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.selectDepartment")} required={true} />
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <select className="form-select"
                                        name='departmentId'
                                        value={designationFromData?.departmentId}
                                        onChange={(e) => handleChange(e)}
                                        disabled=''
                                    >
                                        <option value="" hidden selected>Select Department...</option>
                                        {departmentList && departmentList?.length > 0 &&
                                            departmentList?.map((department, index) => <option
                                                value={department?._id}
                                            >
                                                {department?.name}
                                            </option>
                                            )}
                                    </select>
                                    {errors?.departmentId && (
                                        <CustomError text={errors.departmentId} />
                                    )}
                                </div>
                            </div>
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.name")} required={true} />
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <Input
                                        type="text"
                                        placeholder={t("placeholders.designationName")}
                                        value={designationFromData?.designationName || ''}
                                        name="designationName"
                                        onChange={(e) => handleChange(e)}
                                    />

                                    {errors?.designationName && (
                                        <CustomError text={errors.designationName} />
                                    )}
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.shortName")} required={true} />
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <Input
                                        type="text"
                                        placeholder={t("placeholders.shortName")}
                                        value={designationFromData?.shortName}
                                        name="shortName"
                                        onChange={(e) => handleChange(e)}
                                    />
                                    {errors?.shortName && (
                                        <CustomError text={errors.shortName} />
                                    )}
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.isActive")} />
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <CustCheckbox
                                        key={"is_active"}
                                        id={`is_active`}
                                        checked={isActive}
                                        handleChange={(e) => setIsActive(!isActive)}
                                        name={"is_active"}
                                    />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="offcanvas-footer">
                    <div className="d-flex align-items-center justify-content-end gap-3">
                        <CustomButton
                            text={selectedDesignation ? t("btns.update") : t("btns.submit")}
                            classNames={"btn btn-org-gradient"}
                            handleClick={() => createDesignation()}
                            isLoading={selectedDesignation ? updateDesignationPending : addDesignationPending}
                        />
                    </div>
                </div>
            </div>
        </>
    )
}

export default CreateUpdateDesignation
