import React, { useEffect, useState } from 'react'
import DesignationTable from './DesignationTable'
import { useDispatch, useSelector } from 'react-redux'
import { designationAction } from '../../../core/resource/actions'
import ApproveRejectModal from '../../modal/ApproveRejectModal'
import { pagination } from '../../../utils/Constants.config'
import { useCustomTranslation } from '../../../translation'
import { CustomButton } from '../../globalComponents'
import CreateUpdateDesignation from './CreateUpdateDesignation'

const Designation = () => {
  const {t}=useCustomTranslation()
  const dispatch = useDispatch();
  const { 
    getAllDesignationRes,
    getAllDesignationError,
    getAllDesignationPending,
    deleteDesignationPending,
    deleteDesignationRes,
    deleteDesignationError

  } = useSelector(state => state.designation);
  const [designationList, setDesignationList] = useState('')
  const [selectedDesignation, setSelectedDesignation] = useState('')
  const [isOpen, setIsOpen] = useState(false);
  const [isShowDeleteModal,setIsShowDeleteModal]=useState(false)
  const [deleteDesiId, setDeleteDesiId] = useState()
  const [currentPage, setCurrentPage] = useState(pagination?.page);

  useEffect(() => {
    getAllDesignation()
  }, [currentPage])

  const toggleDesignation = () => {
    setIsOpen(!isOpen);
  };
  const getAllDesignation = () => {
    const params = {
      page: currentPage,
      limit: pagination?.limit,
    }
    dispatch(designationAction.getAllDesignationRequest(params));
  }

  useEffect(() => {
    if (!getAllDesignationPending) {
      if (getAllDesignationError) {
        return;
      }
      if (getAllDesignationRes) {
        if (getAllDesignationRes?.code == 200) {
          setDesignationList(getAllDesignationRes?.data)
        }
      }
    }
  }, [
    getAllDesignationRes,
    getAllDesignationError,
    getAllDesignationPending
  ]);

  //delete designation

  const onDeleteCancelDesignation = (type, id) => {
    if (type) {
      let params = {
        _id: deleteDesiId
      }
      dispatch(designationAction?.deleteDesignationRequest(params))
    }
    setIsShowDeleteModal(!isShowDeleteModal)
  }
  const onHandleDeleteModal = (id) => {
    setIsShowDeleteModal(!isShowDeleteModal)
    setDeleteDesiId(id)
  }

  useEffect(() => {
    if (!deleteDesignationPending) {
      if (deleteDesignationError) {
        dispatch(designationAction.handleClearDesignationStore('HANDLE_CLEAR_DELETE_DESIGNATION_STORE'))
        return;
      }
      if (deleteDesignationRes) {
        if (deleteDesignationRes?.code == 200) {
          dispatch(designationAction.handleClearDesignationStore('HANDLE_CLEAR_DELETE_DESIGNATION_STORE'))
          if (currentPage > 1 && designationList?.data?.length === 1) {
            setCurrentPage((pre => pre -= 1))
          } else {
            getAllDesignation()
          }
        }
      }
    }
  }, [
    deleteDesignationRes,
    deleteDesignationError,
    deleteDesignationPending
  ]);


  return (
    <div>
      <div className=" d-flex  justify-content-end ">
      <CustomButton
          classNames={"btn btn-primary gradientBg"}
          handleClick={() => toggleDesignation("createDesignation")}
          text={t("btns.createDesignation")}
        />
      </div>
      <DesignationTable
        designationList={designationList}
        isLoading={getAllDesignationPending}
        setSelectedDesignation={setSelectedDesignation}
        toggleDesignation={toggleDesignation}
        onHandleDeleteModal={onHandleDeleteModal}
        currentPage={currentPage}
        setCurrentPage={setCurrentPage}

      />
      <CreateUpdateDesignation
        getAllDesignation={getAllDesignation}
        setSelectedDesignation={setSelectedDesignation}
        isOpen={isOpen}
        toggleDesignation={toggleDesignation}
        selectedDesignation={selectedDesignation}
      />
       {isShowDeleteModal && (
        <ApproveRejectModal
          onApproveReject={onDeleteCancelDesignation}
          isShow={isShowDeleteModal}
          desc={t("alerts.deletePopup.desc").replace('$1',"Designation")}
          btnRightLoading={deleteDesignationPending}
        />)}
    </div>
  )
}

export default Designation
